#play-list h3.title {
  margin: 0;
  height: 6.133vw;
  line-height: 6.133vw;
  padding: 0 2.667vw;
  font-size: 3.2vw;
  color: #666;
  background-color: #eeeff0;
}
#play-list #play-list-header {
  height: 49.6vw;
  overflow: hidden;
  position: relative;
  width: 100vw;
}
#play-list #play-list-header .play-list-bg {
  width: 100%;
  height: 100%;
  -webkit-filter: blur(5.333vw);
          filter: blur(5.333vw);
  -webkit-transform: scale(1.5);
          transform: scale(1.5);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: 50%;
}
#play-list #play-list-header::after {
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  z-index: 1;
  content: ' ';
  background-color: rgba(0, 0, 0, 0.25);
}
#play-list #play-list-detail {
  display: flex;
  height: 33.6vw;
  position: relative;
  padding: 8vw 2.667vw 8vw 4vw;
  overflow: hidden;
  margin-top: -49.6vw;
}
#play-list #play-list-detail .cover {
  width: 33.6vw;
  height: 33.6vw;
  flex: none;
  background-repeat: no-repeat;
  background-size: cover;
  position: relative;
  z-index: 3;
}
#play-list #play-list-detail .cover::after {
  content: ' ';
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 4.8vw;
  z-index: 2;
  background-image: -webkit-gradient(linear, left top, right top, from(rgba(0, 0, 0, 0)), to(rgba(0, 0, 0, 0.2)));
  background-image: linear-gradient(90deg, rgba(0, 0, 0, 0), rgba(0, 0, 0, 0.2));
}
#play-list #play-list-detail .cover .list-play-count {
  position: absolute;
  right: 0.533vw;
  top: 0;
  z-index: 3;
  padding-left: 4vw;
  color: #fff;
  font-size: 3.2vw;
  line-height: 4.8vw;
}
#play-list #play-list-detail .cover .list-icon {
  position: absolute;
  z-index: 3;
  top: 2.667vw;
  left: 0;
  padding: 0 2.133vw;
  height: 4.533vw;
  color: #fff;
  font-size: 2.4vw;
  text-align: center;
  line-height: 4.533vw;
  background-color: rgba(217, 48, 48, 0.8);
  border-top-right-radius: 4.533vw;
  border-bottom-right-radius: 4.533vw;
}
#play-list #play-list-detail .play-list-info {
  position: relative;
  margin-left: 4.267vw;
  z-index: 3;
}
#play-list #play-list-detail .play-list-info .play-list-name {
  padding-top: 1px;
  font-size: 4.533vw;
  line-height: 1.3;
  color: #fefefe;
  height: 11.733vw;
  -webkit-box-pack: center;
}
#play-list #play-list-detail .play-list-info .play-list-author {
  position: relative;
  margin-top: 5.333vw;
  width: 38.667vw;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  color: hsla(0, 0%, 100%, 0.7);
}
#play-list #play-list-detail .play-list-info .play-list-author .avatar {
  display: inline-block;
  width: 8vw;
  height: 8vw;
  border-radius: 50%;
  vertical-align: middle;
  margin-right: 1.333vw;
  background-size: cover;
  background-repeat: no-repeat;
}
#play-list #play-list-intro {
  position: relative;
  margin: 0 2.667vw 0 4vw;
  padding-top: 2.667vw;
  line-height: 5.067vw;
  color: #666;
}
#play-list #play-list-intro .play-list-tags {
  margin-bottom: 2.667vw;
  line-height: 5.333vw;
  margin-right: -2.667vw;
}
#play-list #play-list-intro .play-list-tags .play-list-tag {
  display: inline-block;
  margin-right: 2.667vw;
  padding: 0 1.867vw;
  font-size: 3.2vw;
  border: 1px solid rgba(0, 0, 0, 0.1);
  border-radius: 2.933vw;
}
#play-list #play-list-intro .intro-dtl-box {
  position: relative;
  padding-bottom: 4.8vw;
  line-height: 5.067vw;
}
#play-list #play-list-intro .intro-dtl-box .intro-dtl {
  word-wrap: break-word;
  white-space: normal;
}
#play-list #play-list-intro .intro-dtl-box .icon-btn {
  position: absolute;
  bottom: 0.8vw;
  right: 0;
  color: #c9c9c9;
}
#play-list #play-list-intro .intro-dtl-box .icon-btn:hover {
  color: #999;
}
