body {
  background-color: #fff;
}
.highlight {
  color: #507daf;
}
.loading {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
}
.label {
  height: 8vw;
  line-height: 8vw;
  text-align: center;
  color: #888;
}
#main .am-tabs-top {
  position: fixed;
  width: 100%;
  height: calc(100% - 22.4vw);
  top: 22.4vw;
}
.fix-header {
  z-index: 1000;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
}
.fix-header .brand {
  width: 100%;
  height: 22.4vw;
  background-color: #d43c33;
}
.line-3 {
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 3;
}
.line-2 {
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 2;
}
.new-songs .new-songs-item {
  padding-left: 2.667vw;
  display: flex;
  flex: 1 1;
  position: relative;
}
.new-songs .new-songs-item::after {
  position: absolute;
  content: '';
  bottom: 0;
  left: 0;
  width: 100%;
  border-bottom: 1px solid rgba(0, 0, 0, 0.1);
}
.new-songs .new-songs-item .new-songs-rate {
  display: flex;
  margin-left: -2.667vw;
  align-items: center;
  justify-content: center;
