  color: #999;
  font-size: 4.533vw;
  flex-shrink: 0;
}
.new-songs .new-songs-item .new-songs-rate.rate-top-marked {
  color: #df3436;
}
.new-songs .new-songs-item .new-songs-info {
  padding: 1.6vw 0;
