}
.new-songs .new-songs-item .new-songs-info .new-songs-alias {
  color: #888;
}
.new-songs .new-songs-item .new-songs-info .new-songs-title {
  font-size: 4.533vw;
  line-height: 6.133vw;
  margin-bottom: 1.6vw;
