  text-overflow: ellipsis;
}
.new-songs .new-songs-item .new-songs-info .new-songs-extra {
  font-size: 3.2vw;
  color: #888;
  overflow: hidden;
  text-overflow: ellipsis;
}
.new-songs .new-songs-item .play-button {
  display: flex;
  align-items: center;
  padding: 0 2.667vw;
}
.new-songs .new-songs-item .play-button .btn-icon {
  background: url('//s3.music.126.net/mobile-new/img/index_icon_2x.png?5207a28c3767992ca4bb6d4887c74880=') no-repeat;
  background-size: 44.267vw 25.867vw;
  display: inline-block;
  width: 5.867vw;
  height: 5.867vw;
  background-position: -6.4vw 0;
}
.recommend {
  padding-top: 5.333vw;
}
.recommend .remd-tl {
  position: relative;
  padding-left: 2.4vw;
  margin-bottom: 3.733vw;
  font-size: 4.533vw;
  height: 5.333vw;
  line-height: 5.333vw;
}
.recommend .remd-tl::after {
  content: ' ';
  position: absolute;
  left: 0;
  top: 50%;
  margin-top: -2.4vw;
  width: 0.533vw;
  height: 4.267vw;
  background-color: #d33a31;
}
.recommend .remd-songs {
  position: relative;
  padding-bottom: 6.4vw;
}
.recommend .remd-songs .remd-list {
  display: flex;
  flex-wrap: wrap;
}
.recommend .remd-songs .remd-list .remd-li {
  display: block;
  margin-bottom: 4.267vw;
  width: 33.3%;
  padding-left: 1px;
  padding-right: 1px;
  box-sizing: border-box;
}
.recommend .remd-songs .remd-list .remd-li .remd-img {
  position: relative;
  padding-bottom: 100%;
}
.recommend .remd-songs .remd-list .remd-li .remd-img > .u-img {
  position: absolute;
  width: 100%;
  left: 0;
  top: 0;
  z-index: 1;
}
.recommend .remd-songs .remd-list .remd-li .remd-img .u-earp {
  background-image: url(data:image/svg+xml;base64,);
}
.recommend .remd-songs .remd-list .remd-li .remd-img .remd_lnum {
  position: absolute;
  right: 1.333vw;
  top: 0.533vw;
  z-index: 3;
  padding-left: 3.467vw;
  color: #fff;
  font-size: 3.2vw;
  background-position: 0;
  background-repeat: no-repeat;
  background-size: 2.933vw 2.667vw;
  text-shadow: 1px 0 0 rgba(0, 0, 0, 0.15);
}
.recommend .remd-songs .remd-list .remd-li .remd-text {
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  overflow: hidden;
  padding: 1.6vw 0.533vw 0 1.6vw;
  min-height: 8vw;
  line-height: 1.2;
  font-size: 3.467vw;
}
.top .top-header {
  width: 100vw;
  height: 38.9vw;
  position: relative;
  background: url(//s3.music.126.net/mobile-new/img/hot_music_bg_2x.jpg?f01a252389c26bcf016816242eaa6aee=) no-repeat;
  background-size: cover;
  padding-left: 5.333vw;
  padding-top: 8vw;
}
.top .top-header .top-logo {
  width: 37.867vw;
  height: 17.867vw;
  background-position: -6.4vw -8vw;
  background-image: url(//s3.music.126.net/mobile-new/img/index_icon_2x.png?5207a28c3767992ca4bb6d4887c74880=);
  background-size: 44.267vw 25.867vw;
}
.top .top-header .top-time {
  margin-top: 2.667vw;
  color: hsla(0, 0%, 100%, 0.8);
  font-size: 3.2vw;
  -webkit-transform: scale(0.91);
          transform: scale(0.91);
  -webkit-transform-origin: left top;
          transform-origin: left top;
}
.search-page .input-box {
  position: relative;
  padding: 4vw 2.667vw;
}
.search-page .input-box::after {
  position: absolute;
  content: '';
  bottom: 0;
  left: 0;
  width: 100%;
  border-bottom: 1px solid rgba(0, 0, 0, 0.1) /* #ddd */;
}
.search-page .input-box .input-cover {
  position: relative;
  width: 100%;
  height: 8vw;
  padding: 0 8vw;
  box-sizing: border-box;
  background: #ebecec;
  border-radius: 8vw;
}
.search-page .input-box .input-cover .search-icon {
  position: absolute;
  color: #c9c9c9;
  top: 2.133vw;
  left: 2.133vw;
}
.search-page .input-box .input-cover .clear-icon {
  color: #bcbdbd;
  position: absolute;
  top: 2.133vw;
  right: 2.133vw;
}
.search-page .input-box .input-cover .placeholder {
  font-size: 3.733vw;
  color: #c9c9c9;
  position: absolute;
  background: transparent;
  pointer-events: none;
  left: 8vw;
  top: 2.133vw;
}
.search-page .input-box .input-cover .input-content {
  width: 100%;
  height: 8vw;
  line-height: 4.8vw;
  background: transparent;
  font-size: 3.733vw;
  color: #333;
  outline: none;
  border: none;
}
.search-page .search-tip {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  word-break: normal;
}
.search-page .search-tip .search-info {
  height: 13.333vw;
  margin-left: 2.667vw;
  padding-right: 2.667vw;
  font-size: 4vw;
  line-height: 13.333vw;
  color: #507daf;
  border-bottom: 1px solid rgba(0, 0, 0, 0.1);
}
.search-page .search-tip .tips {
  margin: 0;
  list-style: none;
  width: 100%;
  display: flex;
  align-items: center;
  flex-direction: column;
  padding: 0;
}
.search-page .search-tip .tips .tip {
  width: 100%;
  display: flex;
  height: 12vw;
  padding-left: 2.667vw;
  align-items: center;
}
.search-page .search-tip .tips .tip .icon {
  margin-right: 2.667vw;
  color: #c9c9c9;
}
.search-page .search-tip .tips .tip .tip-content {
  flex: 1 1;
  height: 12vw;
  line-height: 12vw;
  position: relative;
}
.search-page .search-tip .tips .tip .tip-content::after {
  content: '';
  width: 100%;
  position: absolute;
  bottom: 0;
  left: 0;
  border-bottom: 1px solid rgba(0, 0, 0, 0.1);
}
.search-page .search-default .search-hot {
  padding: 4vw 2.667vw 0;
}
.search-page .search-default .search-hot .title {
  font-size: 3.2vw;
  line-height: 3.2vw;
  color: #666;
}
.search-page .search-default .search-hot .list {
  padding: 0;
  list-style: none;
  margin: 2.667vw 0 1.867vw;
}
.search-page .search-default .search-hot .list .list-item {
  display: inline-block;
  height: 8.533vw;
  margin-right: 2.133vw;
  margin-bottom: 2.133vw;
  padding: 0 3.733vw;
  font-size: 3.733vw;
  line-height: 8.533vw;
  color: #333;
  border: 1px solid rgba(0, 0, 0, 0.1);
  border-radius: 4.267vw;
}
.search-page .search-default .search-history .list {
  padding: 0;
  margin: 0;
  padding-left: 10.667vw;
}
.search-page .search-default .search-history .list .list-item {
  display: flex;
  height: 12vw;
  align-items: center;
  position: relative;
  border-bottom: 1px solid rgba(0, 0, 0, 0.1);
}
.search-page .search-default .search-history .list .list-item .item-content {
  display: inline-block;
  flex: 1 1;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  word-break: normal;
}
.search-page .search-default .search-history .list .list-item .icon {
  margin: 0 2.667vw;
  color: #c9c9c9;
}
.search-page .search-default .search-history .list .list-item .prefix-icon {
  position: absolute;
  left: -9.6vw;
}
.loading {
  z-index: 100;
  background: url(data:image/gif;base64,) 50% no-repeat;
  background-size: 5.333vw;
